*{
  font-family: Helvetica, sans-serif, Arial;
  
}
body{
  padding: 50px;
  
}

input{
  font-size: 1.2rem;
  width: 100%;
  color: gray;
}
.card-grid{ 
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.card{
  
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}
.card p{
  
  text-align: center;
  
}
.card img{
  max-height: 160px;
}
